body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.formDiv{
  margin: 10px 290px;
  width: 362px;
  padding: 25px;
  background: #f1f1f1;
  border: 1px solid #e5e5e5;
  div {
    position: relative;
    margin: 0 0 1.5em;
  }
}
label {
  display: inline-block;
  width: 100%;
  position: relative;
  margin: 0 0 .5em;
  font-weight: bold;
  color: #222;
}


/* regular input */
input[type=email], input[type=number], input[type=password], input[type=tel], input[type=text], input[type=url] {
  display: inline-block;
  height: 29px;
  margin: 0;
  padding: 0 8px;
  background: #fff;
  border: 1px solid #d9d9d9;
  border-top: 1px solid #c0c0c0;
  box-sizing: border-box;
  border-radius: 1px;
  width: 80%;
  &:hover {
    border: 1px solid #b9b9b9;
    border-top: 1px solid #a0a0a0;
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
  }
  &:focus {
    outline: none;
    border: 1px solid #4d90fe;
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.3);
  }
}

.checkbox span{
  font-weight: normal;
}




/* button */
.g-button {
  display: inline-block;
  min-width: 46px;
  text-align: center;
  color: #444;
  font-size: 11px;
  font-weight: bold;
  height: 27px;
  padding: 0 8px;
  line-height: 27px;
  border-radius: 2px;
  @include transition(all 0.218s);
  border: 1px solid #dcdcdc;
  background-color: #f5f5f5;
  @include background-image(linear-gradient(top, #f5f5f5, #f1f1f1));
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  cursor: default;
}
.g-button {
  height: 29px;
  line-height: 29px;
  vertical-align: bottom;
  margin: 0;
  &:hover {
    border: 1px solid #c6c6c6;
    color: #333;
    text-decoration: none;
    @include transition(all 0.0s);
    background-color: #f8f8f8;
    @include background-image(linear-gradient(top, #f8f8f8, #f1f1f1));
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  }
  &:active {
    background-color: #f6f6f6;  
    @include background-image(linear-gradient(top, #f6f6f6, #f1f1f1));
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.1);
  }
  &:visited {
    color: #666;
  }
}
/* blue button */
.g-button-submit {
  border: 1px solid #3079ed;
  color: #fff;
  text-shadow: 0 1px rgba(0,0,0,0.1);
  background-color: #4d90fe;
  @include background-image(linear-gradient(top, #4d90fe, #4787ed));
  &:hover {
    border: 1px solid #2f5bb7;
    color: #fff;
    text-shadow: 0 1px rgba(0,0,0,0.3);
    background-color: #357ae8;
    @include background-image(linear-gradient(top, #4d90fe, #357ae8));
  }
  &:active {
    background-color: #357ae8;
    @include background-image(linear-gradient(top, #4d90fe, #357ae8));
    box-shadow: inset 0 1px 2px rgba(0,0,0,0.3);
  }
}



